@use 'variables' as v;
@use 'mixins' as m;
@use 'sass:color';

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    background-color: color.adjust(v.$gris-oscuro, $lightness: 5%);
    color: v.$blanco;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    @include m.tablet {
        height: 100%;
    }
}
*, *:before, *:after {
    box-sizing: inherit;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}
