@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';

.tabs {
    @include m.tablet {
        width: auto;
        display: flex;
        margin-bottom: 0;
        justify-content: center;
        gap: 1rem;
    }

    button {
        background-color: v.$azul;
        border: none;
        color: v.$blanco;
        padding: 1rem;
        display: block;
        width: 100%;
        margin-bottom: 1rem;
        text-transform: uppercase;
        font-weight: v.$bold;
        border-radius: 1rem;
        transition: background-color .3s ease;

        &:hover {
            cursor: pointer;
            background-color: color.adjust(v.$azul, $lightness: -10%);
        }

        &.activo {
            background-color: v.$blanco;
            color: v.$azul;
        }
    }
}